
<template>
  <f7-page v-if="data">
    <p id="breadcrumb">
      <a @click="$router.go(-1)">Home</a>
      <a href>{{ data && data.title.length > 33 ? data.title.substr(0, 30) + '...' : data.title }}</a>
    </p>

    <f7-card class="demo-card-header-pic">
      <f7-card-header class="no-border" valign="bottom">
        <img :src="data.full_image" width="100%" />
      </f7-card-header>
      <f7-card-content>
        <b class="title">{{ data.title }}</b>
        <p class="date"> : {{ data.time_format }}</p>
        <p class="date">: {{ data.author.name }}</p>
        <p class="date">: {{ data.categories }}</p>
        <div class="description" v-html="data.description"></div>
      </f7-card-content>
    </f7-card>
  </f7-page>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      data: null
    };
  },
  mounted() {
    setTimeout(() => {
      this.getArticleInfo();
    }, 50);
  },
  methods: {
    getArticleInfo() {
      let url = "/get-article-details/" + this.$route.params.id;
      this.$f7.preloader.show();
      this.$http.get(url).then(({ data }) => {
        this.data = data;
        this.$f7.preloader.hide();
      });
    }
  }
};
</script>
